      h2 3.6.	Instrumentos y técnicas de recolección de información

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La recolección de información durante y después de un recorrido turístico es una práctica clave para evaluar la calidad del servicio, identificar oportunidades de mejora y garantizar la sostenibilidad de la experiencia. Este proceso permite comprender tanto la percepción del visitante como el impacto generado en las comunidades anfitrionas y en el entorno natural. Según la Organización Mundial del Turismo (OMT, 2019), la evaluación de la experiencia turística debe basarse en métodos integrales que combinen la satisfacción del visitante con indicadores de sostenibilidad social, ambiental y económica. En el turismo étnico y comunitario, esto implica incorporar la voz de todos los actores —
      p visitantes, guías, familias anfitrionas y autoridades locales—, reconociendo que cada perspectiva contribuye al fortalecimiento del servicio turístico.
      p Los instrumentos más utilizados para la recolección de información son:

    separador

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Los instrumentos de recolección de información deben ser co-diseñados junto con la comunidad, mediante procesos participativos que reconozcan y respeten sus sistemas de conocimiento y sus formas propias de evaluar la experiencia. Las preguntas no deben centrarse únicamente en la satisfacción del turista, sino también en el cumplimiento de los acuerdos comunitarios, como el respeto a los sitios sagrados, la compra justa de artesanías y el comportamiento responsable.
      p Además, pueden incorporarse métodos visuales y narrativos, como dibujos, mapas simbólicos o relatos personales, que resultan más inclusivos y expresivos que los cuestionarios escritos tradicionales.
      p Estas estrategias enriquecen la comprensión de la experiencia turística y fortalecen la participación activa y la apropiación cultural de todos los actores involucrados. A continuación se presenta de manera gráfica la utilidad específica de cada uno de los instrumentos de recolección de información.

